import img1 from '../assets/weather.png';
import { useState } from 'react';
import { Link } from 'react-router-dom';
export default function Navbar() {
    const a = (para) => {
        alert(para)
    }

    const [toggle, setToggle] = useState(false);

    const nav = () => {

        if (toggle === true) {
            setToggle(false);
        }
        else {
            setToggle(true)
        }
    }
    return (
        <>
            <section className="navbar navbar-expand ">
                <div className="container">
                    <div className="nav-btn" onClick={() => nav()}>
                        <i className="fas fa-bars"></i>
                    </div>
                    <div className={toggle ? 'collapse navbar-collapse nav-sticky': 'collapse navbar-collapse'} id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="page" to="/newsprk">Home</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link" to="/newsprk/about" aria-current="page">
                                    About
                                </Link>
                            </li>
                            <li className="nav-item dropdown">
                                <Link className="nav-link dropdown-toggle" to="#" >
                                    Posts
                                </Link>
                                <ul className="dropdown-menu">
                                    <li><Link className="dropdown-item" to="#">Action</Link></li>
                                    <li><Link className="dropdown-item" to="#">Another action</Link></li>
                                    <li><hr className="dropdown-divider" /></li>
                                    <li><Link className="dropdown-item" to="#">Something else here</Link></li>
                                </ul>
                            </li>
                            <li className="nav-item dropdown">
                                <Link className="nav-link dropdown-toggle" to="#" >
                                    Categories
                                </Link>
                                <ul className="dropdown-menu">
                                    <li><Link className="dropdown-item" to="#">Action</Link></li>
                                    <li><Link className="dropdown-item" to="#">Another action</Link></li>
                                    <li><hr className="dropdown-divider" /></li>
                                    <li><Link className="dropdown-item" to="#">Something else here</Link></li>
                                </ul>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="#">World</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="#">Sports</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to="/newsprk/contact">Contact</Link>
                            </li>
                        </ul>
                        <div className="d-flex align-self-center side-nav">
                            <div className="user-area">
                                <i className="fas fa-search" onClick={() => a('Search')}></i> <i className="fas fa-user-circle"></i>
                            </div>
                            <div className="language">

                                <li className="nav-item dropdown">
                                    <Link className="nav-link dropdown-toggle" to="#" >
                                        English
                                    </Link>
                                    <ul className="dropdown-menu">
                                        <li><Link className="dropdown-item" to="#">Spanish</Link></li>
                                        <li><Link className="dropdown-item" to="#">China</Link></li>
                                        <li><Link className="dropdown-item" to="#">Spanish</Link></li>
                                        <li><Link className="dropdown-item" to="#">Spanish</Link></li>
                                        <li><Link className="dropdown-item" to="#">Nepali</Link></li>
                                    </ul>
                                </li>
                            </div>
                            <div className="weather-container d-flex ">
                                <div className="img-wrapper align-self-center">
                                    <img src={img1} alt="" srcset="" />
                                </div>
                                <div className="weather align-self-center">
                                    <h3>13</h3>
                                    <p>San Francisco</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}
