
export default function Singlepost({ img, title, par }) {
    return (
        <>
            <div className="img-wrapper">
                <img src={img} alt="" className='img-fluid' />
            </div>
            <div className="single-post-type1">
                <div className="meta">
                    <p><span>TECHNOLOGY</span>/ March 26 2021</p>
                </div>
                <h4>
                    <a href="#">{title}</a>
                </h4>
                <p>
                    {par}
                </p>
            </div>
        </>
    )
}
