import Categoriesbox from "./Categoriesbox";
import cat1 from '../assets/category1.jpg';
import cat2 from '../assets/category2.jpg';
import cat3 from '../assets/category3.jpg';
import cat4 from '../assets/category4.jpg';
import cat5 from '../assets/category5.jpg';
import cat6 from '../assets/category6.jpg';

export default function Categories() {
    return (
        <>
            <div className="categories">
                <div className="heading">
                    <h3>Categories</h3>
                </div>
                <div className="row">
                    <div className="col-12 my-2">
                        <Categoriesbox img={cat1} title='Restaurant' />
                    </div>
                    <div className="col-12 my-2">
                        <Categoriesbox img={cat2} title='Entertainment' />
                    </div>
                    <div className="col-12 my-2">
                        <Categoriesbox img={cat3} title='Feature' />
                    </div>
                    <div className="col-12 my-2">
                        <Categoriesbox img={cat4} title='Business' />
                    </div>
                    <div className="col-12 my-2">
                        <Categoriesbox img={cat5} title='Trending' />
                    </div>
                    <div className="col-12 my-2">
                        <Categoriesbox img={cat6} title='Sports' />
                    </div>
                </div>
            </div>
        </>
    )
}
