import mouse from '../assets/mouse.png';

export default function Hero({h4,h1}) {
    return (
        <>
            <section className="hero">
                <div className="container">
                    <div className="text">
                        <h4>{h4}</h4>
                        <h1>{h1}</h1>
                        <div className="mouse">
                            <img src={mouse} alt="" />
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}
