import img from '../assets/videonews.jpg';
import Popularpost from './Popularpost';


export default function Videonews() {
    return (
        <>
            <section className="video-news">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-8 col-md-12">
                            <div className="post-video">
                                <div className="heading">
                                    <h3>Video News</h3>
                                </div>
                                <div className="img-wrapper">
                                    <img src={img} alt="" className='img-fluid' />
                                    <div className="play-btn"><i class="fab fa-youtube"></i></div>
                                </div>
                                <div className="text">
                                    <div className="meta">
                                        <a href="#"><span>TECHNOLOGY</span></a>
                                        /
                                        <a href="#">March 21 2021</a>
                                    </div>
                                    <h4><a href="#">Riots Report Shows London Needs To Maintain Police Numbers, Says Mayor</a></h4>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-12">
                            <Popularpost />
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}
