import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css'
import 'owl.carousel/dist/assets/owl.theme.default.css';
import img1 from '../assets/mixarea.jpg';
import img2 from '../assets/mixarea2.jpg';

export default function Mixarea() {
    const option ={
        loop: true,
        nav: true,
        items: 2,
        margin: 20,
        responsive: {
            0: {
                items: 1,
            },
            769: {
                items: 2,
            },
        }

    }
    return (
        <>
            <section className="mix-area">
                <div className="container">
                    <OwlCarousel className='owl-theme carousel' {...option} >
                        <div className="item">
                            <div className="img-wrapper">
                                <img src={img1} alt="" />
                                <div className="text">
                                    <div className="meta">
                                        <a href="#">TECHNOLOGY</a> / <a href="#">March 26 2021</a>
                                    </div>
                                    <h4><a href="#">Success is not a good food failure makes you humble</a></h4>
                                </div>
                            </div>
                        </div>
                        <div className="item">
                            <div className="img-wrapper">
                                <img src={img2} alt="" />
                                <div className="text">
                                    <div className="meta">
                                        <a href="#">TECHNOLOGY</a> / <a href="#">March 26 2021</a>
                                    </div>
                                    <h4><a href="#">Success is not a good food failure makes you humble</a></h4>
                                </div>
                            </div>
                        </div>
                    </OwlCarousel>

                </div>
            </section>
        </>
    )
}
