import Hero from "./Hero";
import Follow from "./Follow";
import Newsletter from "./Newsletter";
import Adbanner from "./Adbanner";

export default function Contact() {

    return (
        <>
            <Hero h4='contact us' h1="Let's contact" />
            <section className="location">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-4 col-md-12">
                            <div className="location-wrapper">
                                <h2>Headquarters</h2>
                                <p>Location:</p>
                                <h3>44 Canal Center Plaza #200 <br />Alexandria, VA 22314, USA</h3>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-12">
                            <div className="location-wrapper">
                                <h2>Headquarters</h2>
                                <p>Location:</p>
                                <h3>44 Canal Center Plaza #200 <br />Alexandria, VA 22314, USA</h3>
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-12">
                            <div className="location-wrapper">
                                <h2>Headquarters</h2>
                                <p>Location:</p>
                                <h3>44 Canal Center Plaza #200 <br />Alexandria, VA 22314, USA</h3>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <section className="maps">
                <div className="container">
                <iframe width="100%" height="450" frameborder="0" scrolling="no" marginheight="0" marginwidth="0" src="https://maps.google.com/maps?width=100%25&amp;height=400&amp;hl=en&amp;q=1%20Grafton%20Street,%20Dublin,%20Ireland+(My%20Business%20Name)&amp;t=&amp;z=14&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"></iframe>
         
                </div>
            </section>
            <section className="contact-form">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-8">
                            <form action="">
                                <h2>Let’s work together!<br />
                                    Fill out the form.</h2>
                                    <div className="row">
                                        <div className="col-lg-6">
                                            <input type="text" placeholder='Full Name' name='name'/>
                                        </div>
                                        <div className="col-lg-6">
                                            <input type="text" placeholder='Subject'name='subject'/>
                                        </div>
                                        <div className="col-lg-6">
                                            <input type="email" placeholder='Email Adress'name='email'/>
                                        </div>
                                        <div className="col-lg-6">
                                            <input type="number" name="phone" id="phone" placeholder='Phone Numvber'/>
                                        </div>
                                        <div className="col-lg-12">
                                            <textarea name="" id=""  rows="10" placeholder='Tell us about your message...'></textarea>
                                        </div>
                                        <div className="col-lg-12">
                                            <div className="btn submit">send message</div>
                                        </div>
                                    </div>
                            </form>
                        </div>
                        <div className="col-lg-4">
                            <Follow />
                            <Newsletter />
                        </div>
                    </div>
                </div>
            </section>
            <section className="ad">
                <div className="container">
                    <Adbanner/>
                </div>
            </section>
        </>
    )
}
