

export default function Asidepostsbox({ h1, img, title, count }) {
    return (
        <>
            <div className="col-12 d-flex my-2 asidepost-box">
                <div className="img-wrapper">
                    <img src={img} alt="" srcset="" className='img-fluid' />
                    <h1 className='align-self-center'>{h1}</h1>
                </div>
                <div className="aside-post-text">
                    <div className="meta">
                        <p><span>TECHNOLOGY</span> / March 26 2021</p>
                    </div>
                    <h4><a href="#">{title}</a></h4>
                </div>

                <div className="type-count align-self-center">{count}</div>
            </div>
        </>
    )
}
