import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import img1 from '../assets/feature1.jpg';
import img2 from '../assets/feature2.jpg';
import img3 from '../assets/feature3.jpg';

export default function Featurecarouselarea() {

    const option = {
        loop: true,
        margin: 20,
        nav: false,
        items: 4,
        dots: true,
        autoplay: true,
        dots: false,
        responsive: {
            0: {
                items: 1,
            },
            575: {
                items: 2,
            },
            993: {
                items: 4,
            },
        },
    }

    return (
        <>

            <section className="feature-carousel-area">
                <div className="container">
                    <div className="heading">
                        <h3>Feature News</h3>
                    </div>
                    <OwlCarousel
                        className='owl-theme'
                        {...option}
                    >
                        <div class='item'>
                            <div className="img-wrapper">
                                <img src={img1} alt="" className='img-fluid' />
                                <div className="feature-carousel-text">
                                    <div className="meta">
                                        <a href="#">TECHNOLOGY</a> / <a href="#">March 26, 2020</a>
                                    </div>
                                    <h4><a href="#">Best garden wing supplies for the horticu ltural</a></h4>
                                </div>
                            </div>

                        </div>
                        <div class='item'>
                            <div className="img-wrapper">
                                <img src={img2} alt="" className='img-fluid' />
                                <div className="feature-carousel-text">
                                    <div className="meta">
                                        <a href="#">TECHNOLOGY</a> / <a href="#">March 26, 2020</a>
                                    </div>
                                    <h4><a href="#">Copa America: Luis Suarez from devastated US</a></h4>
                                </div>
                            </div>

                        </div>
                        <div class='item'>
                            <div className="img-wrapper">
                                <img src={img3} alt="" className='img-fluid' />
                                <div className="feature-carousel-text">
                                    <div className="meta">
                                        <a href="#">TECHNOLOGY</a> / <a href="#">March 26, 2020</a>
                                    </div>
                                    <h4><a href="#">Best garden wing supplies for the horticu ltural</a></h4>
                                </div>
                            </div>

                        </div>
                        <div class='item'>
                            <div className="img-wrapper">
                                <img src={img2} alt="" className='img-fluid' />
                                <div className="feature-carousel-text">
                                    <div className="meta">
                                        <a href="#">TECHNOLOGY</a> / <a href="#">March 26, 2020</a>
                                    </div>
                                    <h4><a href="#">Japan’s virus success has puzzled the world. Is its luck running out?</a></h4>
                                </div>
                            </div>

                        </div>
                    </OwlCarousel>
                </div>
            </section>
        </>
    )
}
