

export default function Navsliderbox({ img, title }) {
    return (
        <>
            <div className="box d-flex">
                <div className="img-wrapper">
                    <img src={img} alt="" srcset="" className="ing-fluid" />
                </div>
                <div className="text">
                    <div className="title">
                        <h4><a href="#">{title}</a></h4>
                        <p>People have been infected</p>
                    </div>
                </div>
            </div>
        </>
    )
}
