import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css'
import 'owl.carousel/dist/assets/owl.theme.default.css';
import Asidepostsbox from './Asidepostsbox';

export default function Mostshare() {
    const option = {
        loop: true,
        items: 1,
        margin: 0,
        nav: true,
        dots: false,
    }
    return (
        <>
            <aside className="most-share">
                <div className="heading">
                    <h3>
                        Most Share
                    </h3>
                </div>
                <OwlCarousel className='most-share-carousel owl-theme' {...option}>

                    <div className="item">
                        <Asidepostsbox h1='1' title='Nancy zhang a chinese busy woman and dhaka' />
                        <Asidepostsbox h1='2' title='Nancy zhang a chinese busy woman and dhaka' />
                        <Asidepostsbox h1='3' title='Nancy zhang a chinese busy woman and dhaka' />
                        <Asidepostsbox h1='4' title='Nancy zhang a chinese busy woman and dhaka' />
                        <Asidepostsbox h1='5' title='Nancy zhang a chinese busy woman and dhaka' />
                    </div>
                    <div className="item">
                        <Asidepostsbox h1='6' title='Nancy zhang a chinese busy woman and dhaka' />
                        <Asidepostsbox h1='7' title='Nancy zhang a chinese busy woman and dhaka' />
                        <Asidepostsbox h1='8' title='Nancy zhang a chinese busy woman and dhaka' />
                        <Asidepostsbox h1='9' title='Nancy zhang a chinese busy woman and dhaka' />
                        <Asidepostsbox h1='10' title='Nancy zhang a chinese busy woman and dhaka' />
                    </div>
                </OwlCarousel>
            </aside>
        </>
    )
}
