import { Splide, SplideSlide } from '@splidejs/react-splide';
import Navsliderbox from './Navsliderbox';
import img1 from '../assets/nav-slider1.jpg';
import img2 from '../assets/nav-slider2.jpg';
import img3 from '../assets/nav-slider3.jpg';
import img4 from '../assets/nav-slider4.jpg';
import img5 from '../assets/nav-slider5.jpg';
import img6 from '../assets/nav-slider6.jpg';


export default function Navslider() {
    const option = {
        type: 'loop',
        pagination: false,
        perPage: 3,
        height: '5em',
        breakpoints: {
            769: {
                perPage: 2,
            },
            575: {
                perPage: 1,

            },
        }
    }
    return (
        <>
            <section className="nav-slider">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <Splide options={option}>
                                <SplideSlide>
                                    <Navsliderbox img={img1} title='Stimul package will transform the government fundamentally.'/>
                                </SplideSlide>
                                <SplideSlide>
                                    <Navsliderbox img={img2} title='U.S. Response subash says he will label regions by risk of…'/>
                                </SplideSlide>
                                <SplideSlide>
                                    <Navsliderbox  img={img3} title='U.S. Response subash says he will label regions by risk of…'/>
                                </SplideSlide>
                                <SplideSlide>
                                    <Navsliderbox img={img4} title='U.S. Response subash says he will label regions by risk of…' />
                                </SplideSlide>
                                <SplideSlide>
                                    <Navsliderbox img={img5} title='U.S. Response subash says he will label regions by risk of…'/>
                                </SplideSlide>
                                <SplideSlide>
                                    <Navsliderbox  img={img6} title='U.S. Response subash says he will label regions by risk of…'/>
                                </SplideSlide>
                            </Splide>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}
