import catarrow from '../assets/catarrow.png';
export default function Categoriesbox({ img, title }) {
    return (
        <>
            <a href='#' className="categories-box " >
                <div className="img-wrapper">
                    <img src={img} alt="" className='img-fluid img' />
                    <div className="title">
                        <div className="head">
                            <h4>{title}</h4>
                        </div>
                        <div className="arrow">
                            <img src={catarrow} alt="" className='' />
                        </div>
                    </div>
                </div>
            </a>
        </>
    )
}
