import Singlepost from "./Singlepost";
import sportsbig1 from '../assets/sportsbig1.jpg';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css'
import 'owl.carousel/dist/assets/owl.theme.default.css';
import Asidepostsbox from "./Asidepostsbox";
import sports1 from '../assets/sports1.jpg';
import sports2 from '../assets/sports2.jpg';
import sports3 from '../assets/sports3.jpg';
import sports4 from '../assets/sports4.jpg';

export default function Sportsnews() {
    const option = {
        loop: true,
        items: 1,
        margin: 0,
        nav: true,
        dots: false,
    }
    return (
        <>
            <div className="sports-news">
                <div className="heading">
                    <h3>Sports News</h3>
                </div>
                <div className="row">
                    <div className="col-6">
                    <div className="single-post-type1">
                        <Singlepost img={sportsbig1} title='Copa America: Luis Suarez from devastated US' par='The property, complete with 30-seat screening from room, a 100-seat amphitheater and a swimming pond with sandy shower…'/>
                        </div>
                    </div>
                    <div className="col-6">
                <OwlCarousel className='carousel-nav owl-theme'{...option}>
                    <div className="item">
                        <Asidepostsbox img={sports1} title='The billionaire Philan thropist read to learn'/>
                        <Asidepostsbox img={sports2} title='Cheap smartphone sensor could help you'/>
                        <Asidepostsbox img={sports3} title='Ratiffe to be Director of nation talent Trump'/>
                        <Asidepostsbox img={sports4} title='Nancy zhang a chinese busy woman and dhaka'/>
                        <Asidepostsbox img={sports2} title='The billionaire Philan thropist read to learn'/>
                    </div>
                    <div className="item">
                        <Asidepostsbox img={sports2} title='Ratiffe to be Director of nation talent Trump'/>
                        <Asidepostsbox img={sports4} title='The billionaire Philan thropist read to learn'/>
                        <Asidepostsbox img={sports3} title='The billionaire Philan thropist read to learn'/>
                        <Asidepostsbox img={sports2} title='Nancy zhang a chinese busy woman and dhaka'/>
                        <Asidepostsbox img={sports1} title='Cheap smartphone sensor could help you'/>
                    </div>
                </OwlCarousel>
                    </div>
                </div>
            </div>
        </>
    )
}
