import banner from '../assets/banner2.png'

export default function Adbanner() {
    return (
        <>
        <div className="ad">

                <div className="row">
                    <div className="col-12">
                        <img src={banner} alt="" srcset="" className='img-fluid' />
                    </div>
                </div>
        </div>
        </>
    )
}
